import { appendInitialChild, createInstance } from './ReactFiberHostConfig'
import { Fiber } from './ReactInternalTypes'
import {
  FunctionComponent,
  HostComponent,
  HostRoot,
  HostText,
  IndeterminateComponent,
} from './ReactWorkTags'

const appendAllChildren = (parent: Element, workInProgress: Fiber): void => {
  let node: Fiber | null = workInProgress.child
  while (node !== null) {
    if (node.tag === HostComponent || node.tag === HostText) {
      appendInitialChild(parent, node.stateNode)
    } else if (node.child !== null) {
      //å¦æè¯¥å­èç¹ä¸æ¯ä¸ä¸ªHostComponentåç»§ç»­åä¸æ¾
      node.child.return = node
      node = node.child
      continue
    }

    if (node === workInProgress) {
      return
    }

    while (node.sibling === null) {
      //è¯¥å±çº§ææä»¥nodeä¸ºç¶èç¹çå­æ ä¸­ç¦»parentæè¿çdomå·²ç»å®æè¿½å ï¼æ¯æ¶åè¿åå°ä¸å±äº
      /**
       *          FunctionComp A
       * FunctionCompB     FunctionCompC    FunctionCompD
       *                       domE
       * å¦ææ­¤æ¶å¤äºnodeä¸ºdomEï¼é£ä¹å°nodeç½®ä¸ºFunctionCompCå°±ä¼è·³åºè¿ä¸ªå¾ªç¯
       *
       */
      if (node.return === null || node.return === workInProgress) return

      node = node?.return ?? null
    }

    //ä»¥è¯¥nodeä¸ºç¶èç¹çå­æ ä¸­ç¦»parentæè¿çdomå·²ç»å®æè¿½å ï¼å°è¯å¯¹åçº§ä¸­å¶ä»fiberèç¹æ§è¡ç¸åæä½
    node.sibling.return = node.return
    node = node.sibling
  }
}

export const completeWork = (
  current: Fiber | null,
  workInProgress: Fiber
): Fiber | null => {
  const newProps = workInProgress.pendingProps

  switch (workInProgress.tag) {
    case IndeterminateComponent:
    case FunctionComponent:
      return null
    case HostRoot: {
      //todo
      //   const fiberRoot = workInProgress.stateNode
      return null
    }
    case HostComponent: {
      const type = workInProgress.type
      if (current !== null && workInProgress.stateNode != null) {
        //todo
      } else {
        const instance = createInstance(type, newProps)

        //ç±äºæ¯ååºéåï¼å½workInProgressè¿è¡å½é¶æ®µæ¶ï¼
        //å¶ææå­èç¹é½å·²å®æäºéåå½é¶æ®µï¼ä¹å°±æ¯æå³çå¶å­æ çæædomèç¹å·²ç»åå»º
        //æä»¥åªéè¦æå­æ ä¸­ç¦»instanceæè¿çdomèç¹è¿½å å°instanceä¸å³å¯
        appendAllChildren(instance, workInProgress)
        workInProgress.stateNode = instance
      }

      return null
    }
  }

  throw new Error('Not implement')
}
